
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import echarts from 'echarts'

export default {
name: 'PayRollReport',
components: {
CellFormPreview,
Group,
Cell,
Datetime,
XTable,
Grid,
GridItem
},
data () {
return {
showLoading: true,
vocationManageList: [],
value1: '',
payRollReportList: '',
payRollReportDateList: '',
options: [
{text: '', value: ''},
{text: '', value: '2'},
{text: '', value: ''},
{text: '', value: ''},
{text: '', value: '0'}
],
ceshi: {
a: '1'
},
selected: '0'
}
},
mounted () {

console.log(this.ceshi)
this.choosePayRoll(0)
},

methods: {
goTarget (item, index) {
this.$router.push({
name: 'payRollData',
query: {
hrPayrollReportRealSalaryDate: item.hrPayrollReportRealSalaryDate
}
})
},
showPie (itemReport) {
let option = {
tooltip: { // 
trigger: 'item', // ()
formatter: '{a} <br/>{b} : {c} ({d}%)', // 
position: [3, 3]
},
color: ['#48cda6', '#fd87ab', '#ffdf33', '#968ade'], // 
legend: { // 


width: 200, // ,


x: '5%', // 
y: '78%',
bottom: 10,
itemWidth: 10, // 
itemHeight: 10, // 
data: ['', '', ''],
textStyle: { // 
color: 'white', // 
fontSize: 9 
}
},
series: [ // 
{
name: '', // 
type: 'pie', //  pie
center: ['30%', '40%'], //  
radius: ['50%', '70%'], // ,,,0,
itemStyle: { // 
normal: { // normal emphasis 
label: { // 
show: false 
},
labelLine: { // 
show: false 
}
},
emphasis: { // normal emphasis 
label: { // 
show: false,
position: 'center',
textStyle: {
fontSize: '10',
fontWeight: 'bold'
}
}
}
},
data: [
{value: 100, name: ''},
{value: 200, name: ''},
{value: 300, name: ''}
]
}
]
}
let myChart = echarts.init(document.getElementById('main2'))
myChart.setOption(option)
},

choosePayRoll (rowReport) {
this.showPie()
this.payRollReportList = [ {'hrPayrollReportBasePercent': 11} ]
}
}
}



