// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Router from 'vue-router'

/** ******RecordApp*********/
// apprequireapp.jswindowRecordAppwindow.RecordApp
// eslint-disable-next-line no-unused-vars
import RecordApp from 'recorder-core/src/app-support/app'
// Native
import 'recorder-core/src/app-support/app-native-support'
// IOS
import 'recorder-core/src/app-support/app-ios-weixin-support'

import 'recorder-core/src/engine/mp3'
import 'recorder-core/src/engine/mp3-engine'
/** *******Recorder***********/
// app.jsscript
import 'recorder-core'
// ios-weixinamramr
import 'recorder-core/src/engine/beta-amr'
import 'recorder-core/src/engine/beta-amr-engine'

Vue.config.productionTip = false

//  vconsole 
// if (process.env.NODE_ENV === 'development') {
const VConsole = require('vconsole')
new VConsole() // eslint-disable-line
// }

Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



